
*{
  margin: 0;
  padding: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
* {
  margin: 0;
  padding: 0;
}
.nwd-font-bold {
  font-weight: bold;
}
.nwd-text-left {
  text-align: left;
}
.nwd-text-center {
  text-align: center;
}
.nwd-text-right {
  text-align: right;
}
.slide-fade-enter-active {
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}
.slide-fade-leave-active {
  -webkit-transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
  transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter,
.slide-fade-leave-to {
  -webkit-transform: translateY(-15px);
          transform: translateY(-15px);
  opacity: 0;
}
.nwd-alert,
.nwd-alert-default,
.nwd-alert-success,
.nwd-alert-warn,
.nwd-alert-error {
  font-size: 14px;
  position: relative;
  padding: 6px 24px;
  border-radius: 3px;
}
.nwd-alert-title {
  margin-bottom: 5px;
}
.nwd-alert-des {
  font-size: 12px;
}
.nwd-alert-default {
  background: #eaf4fe;
}
.nwd-alert-success {
  background: #e8f9f0;
}
.nwd-alert-warn {
  background: #fff5e6;
}
.nwd-alert-error {
  background: #fdece8;
}
.nwd-alert-close {
  position: absolute;
  right: 10px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  color: #ccc;
  cursor: default;
}
.nwd-alert-close:hover {
  color: #aaa;
}
